.alert {
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0.76);
  height: 100vh;
  width: 100%;
  z-index: 9999999;
  &-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    width: 50%;
    height: 30vh;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
