* {
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
}

html {
  position: relative;
  min-height: 100%;
  font-size: 62.5%;
  @include respond(tab-land) {
    font-size: 56.25%;
  }
  @include respond(tab-port) {
    font-size: 50%;
  }
}

body {
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  margin-bottom: 50vh;
}
