.home {
  width: 70%;
  margin: auto;
  transition: all 0.2s;
  &-about {
    margin-bottom: $gutter;
    &-title {
      text-align: center;
      font-size: 2.4rem;
      color: $color5;
      padding: 0 2rem;
      margin-bottom: $gutter;
    }
    &-description {
      display: flex;
      flex-wrap: wrap;
    }

    &-ph {
      display: inline-block;
      width: 50%;
      @include respond(phone) {
        width: 100%;
      }
    }
    &-text {
      display: inline-block;
      color: $color5;
      background-color: $color1;
      line-height: 3rem;
      width: 50%;
      height: 0;
      padding: 1rem;
      padding-bottom: 25%;
      font-size: 1.6rem;
      overflow-y: auto;
      overflow-x: hidden;
      @include respond(phone) {
        width: 100%;
        padding-bottom: 50%;
      }
    }
  }
}
