.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  background-color: $color1;
  padding: 0.5rem 5rem;
  font-size: 1.5rem;
  box-shadow: 0 1rem 2rem $color2;
  margin-bottom: $gutter;
  z-index: 9999;

  &-logo {
    flex: 0 0 20%;

    &-img {
      height: 6rem;
      border-radius: 50%;
      border: 2px solid $color4;
    }
  }

  &-menu {
    flex: 0 0 40%;
    display: flex;
    justify-content: space-around;
    font-weight: 600;
    @include respond(phone) {
      flex: 0 0 60%;
    }
  }

  &-menu a {
    position: relative;
    color: $color5;

    &::after {
      content: "";
      display: block;
      background-color: #000;
      height: 2px;
      width: 100%;
      position: absolute;
      bottom: -3px;
      transition: all 0.3s;
      transform: scale(0);
    }
    &:hover:after {
      transform: scale(1);
    }
  }
}

.sign {
  cursor: pointer;
  border: none;
  background-color: transparent;
  color: $color5;
  font-weight: 600;
  font-size: 1.3rem;

  & i {
    color: red;
  }
}
